body {
  margin: 0;
  padding: 0;
  font-family: 'Inter', sans-serif;
  background-color: #f4f4f4;
  overflow: hidden;
}

.login-container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
}

.login-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  overflow: hidden;
}

.bg-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #C1F11D 0%, #9dd90d 100%);
  opacity: 0.1;
}

.bg-shapes .shape {
  position: absolute;
  border-radius: 50%;
  background: rgba(193, 241, 29, 0.2);
}

.shape-1 {
  top: -10%;
  left: -5%;
  width: 400px;
  height: 400px;
  animation: float1 10s infinite alternate;
}

.shape-2 {
  bottom: -15%;
  right: -10%;
  width: 500px;
  height: 500px;
  animation: float2 12s infinite alternate;
}

.shape-3 {
  top: 50%;
  left: 50%;
  width: 300px;
  height: 300px;
  transform: translate(-50%, -50%);
  animation: float3 8s infinite alternate;
}

@keyframes float1 {
  0% { transform: translateY(0); }
  100% { transform: translateY(50px); }
}

@keyframes float2 {
  0% { transform: translateY(50px); }
  100% { transform: translateY(0); }
}

@keyframes float3 {
  0% { transform: translate(-50%, -50%) rotate(0deg); }
  100% { transform: translate(-50%, -50%) rotate(360deg); }
}

.login-wrapper {
  position: relative;   
  z-index: 10;


}

.login-form {
  background-color: rgba(255, 255, 255, 0.9);
  padding: 1.5rem;
  border-radius: 12px;
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.1);
  width: 350px;
  backdrop-filter: blur(10px);

}

.form-header {
  text-align: center;
  margin-bottom: 1rem;
}

.login-icon {
  color: #C1F11D;
  margin-bottom: 1rem;
}

.form-header h2 {
  margin: 0;
  color: #333;
  font-weight: 700;
}

.form-group {
  position: relative;
  margin-bottom: 1rem;
}

.input-icon {
  position: absolute;
  left: 12px;
  top: 50%;
  transform: translateY(-50%);
  color: #999;
}

input {
  width: 80%;
  padding: 0.75rem 0.75rem 0.75rem 40px;
  border: 1px solid #ddd;
  border-radius: 8px;
  font-size: 1rem;
  transition: all 0.3s ease;
}

input:focus {
  outline: none;
  border-color: #C1F11D;
  box-shadow: 0 0 0 3px rgba(193, 241, 29, 0.2);
}

.login-button {
  width: 100%;
  padding: 0.75rem;
  background-color: #C1F11D;
  color: #141414;
  border: none;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.login-button:hover:not(:disabled) {
  background-color: #9dd90d;
  transform: translateY(-2px);
}

.login-button:disabled {
  cursor: not-allowed;
  opacity: 0.7;
}

.form-footer {
  display: flex;
  justify-content: center;
  margin-top: 0.5rem;
  font-size: 0.875rem;
}

.create-account {
  color: #9ac11a;
  text-decoration: none;
  font-weight: 300;
}

.create-account:hover {
  text-decoration: underline;
}


.file-upload-wrapper {
  position: relative;
  width: 80%;
}

.file-upload-wrapper input[type="file"] {
  position: absolute;
  left: 0;
  top: 0;
  opacity: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
  z-index: 2;
}

.file-placeholder {
  display: block;
  width: 100%;
  padding: 0.75rem 0.75rem 0.75rem 40px;
  border: 1px solid #ddd;
  border-radius: 8px;
  font-size: 1rem;
  color: #666;
  background-color: white;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.file-upload-wrapper:hover .file-placeholder {
  border-color: #C1F11D;
}


.error-message {
  color: #dc3545;
  text-align: center;
  margin-bottom: 1rem;
}